import React, { Component } from 'react';
import {Grid, Row, Col, Image, Button} from 'react-bootstrap';
import SubmitButton from './submitbutton';
import logo from './images/logo.svg';
import myoracle from './images/myoracle.png'
import ContentEditable from "react-contenteditable";
import './App.css';

class App extends Component {

  constructor(props){
    super(props);

    this.state = { inputtext: "Let's chat!"};
  }

  handleInput = evt => {
    console.log(evt.target.value);

    let wordList = evt.target.value.split(" ");
    console.log(wordList);
    //let finalString = "";
    for (let i = 0; i < wordList.length; i++){
      //coloring text
      if (wordList[i] === "you") {
        wordList[i] = `<span style="color: green;">${wordList[i]}</span>`;
      }
      console.log(`index: ${i} word: ${wordList[i]}`)

      //finalString += wordList[i];
    }

    //this.setState({ inputtext: evt.target.value});
    //this.setState({ inputtext: finalString});
    this.setState({ inputtext: wordList.join(' ')});
  };

  render() {
    return (
      <div>
        <header className="App-header">
          <img src={myoracle} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to My Oracle!</h1>
        </header>

        <br />

        <Grid>
          <Row>
            <Col xs={6} md={4}>
              <p>Come let's chat! Tell me about your day,
                or let's have a conversation about what interests you.
                With time, I can learn!
              </p>
            </Col>
            <Col xs={6} md={4}>
              <Image src={myoracle} style={{ maxHeight: '100%', maxWidth: '100%'}} rounded />
            </Col>
            <Col xsHidden md={4}>
              <div className="col-sm-12 col-xs-3"> {/* mixing regular react tags with react-bootstrap tags...
                                                    may not be the best idea...:D*/}
                  <p><b>Black text I'll try to understand.</b></p>
              </div>
              <div className="col-sm-12 col-xs-3">
                  <p><b><span style={{ color: "green"}}>Green</span> text I know.</b></p>
              </div>
              <div className="col-sm-12 col-xs-3">
                  <p><b><span style={{ color: "blue"}}>Blue</span> text I can learn.</b></p>
              </div>
              <div className="col-sm-12 col-xs-3">
                  <p><b><span style={{ color: "red"}}>Red</span> text I'll find difficult.</b></p>
              </div>
            </Col>
          </Row>

          <br/>


          <Row> {/*User input*/}
            <Col md={3}>
              {/*space*/}
            </Col>
            <Col md={5} mdPush={6}>
              <ContentEditable html={this.state.inputtext} //innerHTML of the editable div
                                  disabled={false} //use true to disable editting
                                  onChange={this.handleInput} //handle innerHTML change
                                  />
            </Col>
            <Col md={1} mdPull={6}>
              <SubmitButton />
            </Col>
            <Col md={3}>
              {/*space*/}
            </Col>
          </Row>
        </Grid>

        <br/>

        <Grid> {/*Footer*/}
          <div className="row">
            <div className="col-sm-4">
              <p>Developed by Ernestas Monkevicius</p>
            </div>
            <div className="col-sm-4">
                {/*sapce*/}
            </div>
            <div className="col-sm-4">
                <ul className="list-inline text-center">
                    <li className="list-inline-item"><a href="#">Settings</a></li>
                    <li className="list-inline-item"><a href="#">Privacy</a></li>
                    <li className="list-inline-item"><a href="#">Terms</a></li>
                </ul>
            </div>
          </div>
        </Grid>
      </div>
    );
  }
}

export default App;



